import React, { Component } from 'react';
import crypto from 'crypto';
import axios from 'axios';

var key = "tHis_iS_pRivaTe_Key";
const encrypt = (err, key) => {
  let cipher = crypto.createCipher('aes-256-cbc', key);
  let encipheredpw = cipher.update(err, 'utf-8', 'hex');
  // encipheredpw = cipher.setAutoPadding(auto_padding=true);
  encipheredpw += cipher.final('hex');
  return encipheredpw;
}

class Register extends Component {
  constructor(props) {
    super(props);
    this.state={
      email: null,  
      password: null,

      eVal: true, //  
      pVal: true,  //  
      ppVal: true
    }
  }

  handleChange = (e) => {  
    //   
    if(e.target.placeholder === "email"){
      if(e.target.value.match(/^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/)){
        this.setState({
          email: e.target.value,
          eVal: true
        })        
      } else{
        this.setState({
          eVal: false
        })
      }
    }

    //    (( ),, ~!@#$%^&*()-_?  )
    else if(e.target.placeholder === ""){
      if(e.target.value.match(/^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,16}$/)
        && (e.target.value.length >= 8) && (e.target.value.length <= 16)) {
        this.setState({
          password: e.target.value,
          pVal: true
        })
      } else{
        this.setState({
          pVal: false
        })
      }
      console.log(this.state.password)
    }

    //  
    else if(e.target.placeholder === " "){
      if(this.state.password === e.target.value) {
        this.setState({
          ppVal: true
        })
      } else{
        this.setState({
          ppVal: false
        })
      }
    }    
  }  

  handleAuth = () => {
    axios.post( 
      'Register', 
      'auth='+true+'&email='+this.state.email, 
      { 'Content-Type': 'application/x-www-form-urlencoded' }
    )
    alert('    .\n(  1~2    .)')
  }

  handleRegister = (e) => {
    //   
    if(this.state.eVal && this.state.pVal && this.state.ppVal){
      axios.post( 
        'Register', 
        'auth='+false+
        '&email='+this.state.email+
        '&password='+encrypt(this.state.password, key)+
        '&key='+document.getElementById('key').value, 
        { 'Content-Type': 'application/x-www-form-urlencoded' }
      )
      .then( response => {
        if(response.data === 'dupError') alert('  .')
        else if(response.data === 'authError') alert(' .')
        else if(response.data === 'complete') {
          alert(' .')
          window.location = "/";
        }
        else alert(response.data)
      }) 
      .catch( response => { console.log('err\n'+response); } ); // ERROR
       
    }
    else{
      alert(' ')
    }
  }

  render() {
    return (
      <div>
        <br/>
        <input placeholder="email" onChange={(e)=>this.handleChange(e)}/>
        <button onClick={this.handleAuth}> </button><br/>
        {!this.state.eVal && <a>   .<br/></a>}<br/>
         <br/>
        <input placeholder="" id="key" onChange={(e)=>this.handleChange(e)}/><br/><br/>

         ("( ), ,   8~16")<br/>
        <input type="password" placeholder="" onChange={(e)=>this.handleChange(e)}/><br/>
        {!this.state.pVal && <a>   .<br/></a>}<br/>
         <br/>
        <input type="password" placeholder=" " onChange={(e)=>this.handleChange(e)}/><br/>
        {!this.state.ppVal && <a> .</a>}<br/><br/>

        <button onClick={this.handleRegister}></button>
      </div>      
    );
  }
}

export default Register;




